.music-player{height:100vh;background-color:#fd79a72c;display:flex;align-items:center;justify-content:center;color:#2d3436}.music-player .container{border-radius:20px;padding:2rem 0 0;width:60%;max-width:30rem;background-color:#fd79a721;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.music-player .container .player-container{width:100%;margin-block-start:6rem;position:relative}.music-player .container .player-container .progress-bar-container{position:absolute;top:0;left:50%;transform:translate(-50%, 0);padding:1rem;width:80%;transition:transform .5s ease;background-color:rgba(255,255,255,.3);border-top-left-radius:10px;border-top-right-radius:10px}.music-player .container .player-container .player-bar{position:relative;display:flex;align-items:center;justify-content:space-around;padding:0 2rem;background-color:#dfe6e9;border-radius:20px;box-shadow:0 20px 20px 0 rgba(252,169,169,.6)}.music-player .container .player-container .player-bar .img{width:80px;height:80px;background-image:url("../assets/music-player/琴弦上.jpg");background-position:center;background-size:cover;border-radius:50%;animation:img-rotate 8s linear infinite;animation-play-state:paused}.music-player .container .player-container .player-bar .navigation{display:flex;justify-content:space-around;flex-grow:1}.music-player .container .player-container .player-bar .navigation button{border:none;opacity:.5}.music-player .container .player-container .player-bar .volume-control-wrapper{display:flex;align-items:center}.music-player .container .player-container .player-bar .volume-control-wrapper i{opacity:.5;cursor:pointer;width:18px}.music-player .container .player-container .player-bar .volume-control-wrapper .volume-control-bar{margin-inline-start:8px;position:relative;vertical-align:middle;width:4rem;border-radius:2px}.music-player .container .player-container.active .progress-bar-container{transform:translate(-50%, -100%)}.music-player .container .player-container.active .player-bar .img{animation-play-state:running}.music-player .dragable-bar-wrapper{width:80%;height:4px;cursor:pointer;background-color:#fff;position:relative;vertical-align:middle;display:flex;align-items:center;border-radius:2px;margin:0 auto}.music-player .dragable-bar-wrapper .dragable-bar{width:20%;height:100%;border-radius:2px;background-color:#d63031}.music-player .dragable-bar-wrapper .dragable-point{position:absolute;top:-100%;left:0;border-radius:50%;border:2px #fff solid;width:12px;height:12px;background-color:#d63031;transform:translate(-50%, 0)}@keyframes img-rotate{0%{transform:rotate(0)}100%{transform:rotate(360deg)}}